import React from "react";

import {
    Box, 
    Typography,
} from '@mui/material';


function TabPanel (props) {
    const { children, value, index, ...other} = props
    
    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}

        </div>
    )
};


export default TabPanel;